import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";

export function defaultConfig(id) {
	am4core.options.commercialLicense = true;
  const colorSet = new am4core.ColorSet();
	const chart = am4core.create(id, am4charts.GaugeChart);

	chart.paddingRight = 0;
	chart.paddingLeft = 0;
	chart.paddingTop = 5;
	chart.paddingBottom = 0;
	chart.marginRight = 0;
	chart.marginLeft = 0;
	chart.marginTop = 5;
	chart.marginBottom = 0;

	chart.innerRadius = -8;
	chart.startAngle = 135;
	chart.endAngle = 405;

	const axis = chart.xAxes.push(new am4charts.ValueAxis());
	axis.min = 0;
	axis.max = 100;
	axis.strictMinMax = true;
	axis.renderer.ticks.template.stroke = '#fff'; //chart.colors.getIndex(6);
	axis.renderer.labels.template.radius = 5;
	axis.renderer.ticks.template.strokeOpacity = .35;
	//axis.renderer.ticks
	axis.renderer.grid.template.disabled = true;
	axis.renderer.ticks.template.length = -12;
	axis.hiddenState.properties.opacity = 1;
	axis.hiddenState.properties.visible = true;
	axis.setStateOnChildren = true;
	axis.numberFormatter.bigNumberPrefixes = [
		{ "number": 1000, "suffix" : "k"},
		{ "number": 1000000, "suffix": "M" },
		{ "number": 1e+9, "suffix": "G" },
	]
	axis.numberFormatter.numberFormat = '#,###.#a'

	const range0 = axis.axisRanges.create();
	range0.value = 0;
	range0.endValue = 50;
	range0.axisFill.fillOpacity = .9;
	range0.axisFill.fill = colorSet.getIndex(0);

	const range1 = axis.axisRanges.create();
	range1.value = 50;
	range1.endValue = 75;
	range1.axisFill.fillOpacity = .9;
	range1.axisFill.fill = colorSet.getIndex(2);

	const range2 = axis.axisRanges.create();
	range2.value = 75;
	range2.endValue = 100;
	range2.axisFill.fillOpacity = .9;
	range2.axisFill.fill = colorSet.getIndex(4);

	const label = chart.radarContainer.createChild(am4core.Label);
	label.isMeasured = false;
	label.horizontalCenter = "middle";
	label.verticalCenter = "bottom";
	label.y = 70;
	label.fontSize = 20;
	label.text = `default`;

	const Title = chart.titles.create();
	Title.text = 'Gauge'; //name !== '' ? name : data ? data.alias : '';
	Title.marginBottom = 15;
	const hand = chart.hands.push(new am4charts.ClockHand());
  hand.showValue(50);
	return chart; 
}
