import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";

export function defaultConfig(id) {
  am4core.options.commercialLicense = true;
  const colorSet = new am4core.ColorSet();
  const chart = am4core.create(id, am4charts.XYChart3D);
  chart.paddingRight = 5;
  chart.paddingLeft = 0;
  chart.paddingTop = 5;
  chart.paddingBottom = 0;
  chart.marginRight = 5;
  chart.marginLeft = 0;
  chart.marginTop = 5;
  chart.marginBottom = 0;

  chart.data = [
    {
      'category': `Default Value`,
      'fillValue': (50/100)*100, // (value/max)*100
      'emptyValue': ((100-25)/100)*100 // ((max-value)/max)*100
    }
  ]

  const title = chart.createChild(am4core.Label);
  title.text = `Max: 100`;
  title.align = 'center';
  const label = chart.createChild(am4core.Label);
  label.text = 'Cylinder Gauge';
  label.align = "center";
  label.isMeasured = false;
  label.x = am4core.percent(50);
  label.horizontalCenter = 'middle';
  label.y = am4core.percent(0);

  const categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
  categoryAxis.dataFields.category = "category";
  categoryAxis.renderer.grid.template.location = 0;
  categoryAxis.renderer.grid.template.strokeOpacity = 0;

  const valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
  valueAxis.renderer.grid.template.strokeOpacity = 0;
  valueAxis.min = -20;
  valueAxis.max = 120;
  valueAxis.strictMinMax = true;
  valueAxis.renderer.baseGrid.disabled = true;
  valueAxis.numberFormatter = new am4core.NumberFormatter();
  valueAxis.numberFormatter.numberFormat = "";

  const series1 = chart.series.push(new am4charts.ConeSeries());
  series1.dataFields.valueY = "fillValue";
  series1.dataFields.categoryX = "category";
  series1.columns.template.width = am4core.percent(80);
  series1.columns.template.fillOpacity = 0.9;
  series1.columns.template.strokeOpacity = 1;
  series1.columns.template.strokeWidth = 2;
  // if(color !== '') {
  //   series1.columns.template.fill = colorSet.getIndex(0);
  //   series1.columns.template.stroke = colorSet.getIndex(0);
  // }

  const series2 = chart.series.push(new am4charts.ConeSeries());
  series2.dataFields.valueY = "emptyValue";
  series2.dataFields.categoryX = "category";
  series2.stacked = true;
  series2.columns.template.width = am4core.percent(80);
  series2.columns.template.fill = am4core.color("#000");
  series2.columns.template.fillOpacity = 0.1;
  series2.columns.template.stroke = am4core.color("#000");
  series2.columns.template.strokeOpacity = 0.2;
  series2.columns.template.strokeWidth = 2;
  return chart;
}
