import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";

export function defaultConfig(id) {
  am4core.options.commercialLicense = true;
  const chart = am4core.create(id, am4charts.XYChart);
  chart.paddingRight = 10;
  chart.paddingLeft = 0;
  chart.paddingTop = 5;
  chart.paddingBottom = 0;
  chart.marginRight = 10;
  chart.marginLeft = 0;
  chart.marginTop = 5;
  chart.marginBottom = 0;
  chart.exporting.menu = new am4core.ExportMenu();
  chart.exporting.menu.verticalAlign = 'bottom';
  chart.exporting.menu.align = 'left';
  // data will come from /api/HistoricalDataItems based on selected data items
  const data = [];
  let price1 = 1000, price2 = 1200;
  const quantity = 30000;
  for (let i = 0; i < 360; i++) {
    price1 += Math.round((Math.random() < 0.5 ? 1 : -1) * Math.random() * 100);
    price2 += Math.round((Math.random() < 0.5 ? 1 : -1) * Math.random() * 100);
    data.push({ date1: new Date(2015, 0, i), price1, price2 });
  }

  chart.data = data;
  // Create axes
  const dateAxis = chart.xAxes.push(new am4charts.DateAxis());
  dateAxis.tooltipDateFormat = "HH:mm:ss";
  dateAxis.baseInterval = {"timeUnit": "second", "count": 1}
  dateAxis.renderer.minGridDistance = 100;
  dateAxis.renderer.grid.template.location = 0.5;
  dateAxis.startLocation = 0.5;
  dateAxis.endLocation = 0.5;
  // loop through based on dataitem props.
  // or use props from new field
  chart.cursor = new am4charts.XYCursor();
  chart.cursor.behavior = "panXY";
  chart.cursor.xAxis = dateAxis;
  //chart.cursor.snapToSeries = series;
  // create y Axis
  const valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
  valueAxis.tooltip.disabled = true;

  valueAxis.renderer.minWidth = 60;

  const valueAxis2 = chart.yAxes.push(new am4charts.ValueAxis());
  valueAxis2.tooltip.disabled = true;
  valueAxis2.renderer.grid.template.strokeDasharray = "2,3";
  valueAxis2.renderer.minWidth = 60;
  // Create series
  const series = chart.series.push(new am4charts.LineSeries());
  series.name = "2015";
  series.dataFields.dateX = "date1";
  series.dataFields.valueY = "price1";
  series.tooltipText = "{valueY.value}";
  series.fill = am4core.color("#e59165");
  series.stroke = am4core.color("#e59165");

  const series2 = chart.series.push(new am4charts.LineSeries());
  series2.name = "2017";
  series2.dataFields.dateX = "date1";
  series2.dataFields.valueY = "price2";
  series2.yAxis = valueAxis2;
  series2.xAxis = dateAxis;
  series2.tooltipText = "{valueY.value}";
  series2.fill = am4core.color("#dfcc64");
  series2.stroke = am4core.color("#dfcc64");
  // Create a horizontal scrollbar with previe and place it underneath the date axis
  chart.scrollbarX = new am4core.Scrollbar();
  chart.scrollbarX.parent = chart.bottomAxesContainer;
  chart.legend = new am4charts.Legend();
  //Title
  const Title = chart.titles.create();
  Title.text = 'Advanced Graph';
  Title.marginBottom = 5;
  return chart; 
}
