import React, { Component } from 'react'
import { connect } from 'react-redux';

class Indicator extends Component {
  static defaultProps = {
    defaultValue: Math.floor(Math.random() * 100),
    defaultBackground: `hsl(0,0%,${Math.floor(Math.random()*20)}%)`
  }

  componentDidMount() {}

  defaultConfig = () => {
    return (
      <div
        className='indicator'
        style={{width: '100%', height: '100%', textAlign: 'center', backgroundColor: this.props.defaultBackground}}
      >
        <span >Indicator</span>
        <br />
        <span>
          {`Value: XX`}
        </span>
      </div>
    )
  }

  config = () => {
    const { labelColor, valueColor, backgroundColor, timestamp } = this.props.tile.properties;
    const id = this.props.tile.properties.dataItem.dataItemId;
    const dataItem = this.props.currentData ? this.props.currentData.object[id] : {alias: 'undefined', value: 'undefined'};
    return (
      <div
        className='indicator'
        style={{width: '100%', height: '100%', textAlign: 'center', backgroundColor: `#${backgroundColor}`}}
      >
        <span style={{color: `#${labelColor}`}}>{dataItem.alias}</span>
        <br />
        <span
          style={{color: `#${valueColor}`}}
        >
          {dataItem.isString ? dataItem.stringValue : `${dataItem.value.toFixed(2)} ${dataItem.unit}`}
        </span>
        <br />
        <span
          style={{color: `#${valueColor}`}}
        >
          {timestamp ? new Date(dataItem.timestamp).toLocaleString() : ''}
        </span>
      </div>
    )
  }

  render () {
    const { type } = this.props.tile.properties;
    return type.default ? this.defaultConfig() : this.config();
  }
}

function mapStateToProps({ currentData }) {
  return { currentData }
}

export default connect(mapStateToProps)(Indicator);
