import React, { Component } from 'react';
import { connect } from 'react-redux';
import { InputText } from 'primereact/inputtext';
import { Dropdown } from 'primereact/dropdown';
import MyColorPicker from './MyColorPicker';
import * as am4core from "@amcharts/amcharts4/core";

const colorSet = new am4core.ColorSet();

class GaugeForm extends Component {
  state = {
    dataItem: null,
    name: '',
    min : '',
    max: '',
    unit: '',
    band1: colorSet.getIndex(0).hex.substring(1),
    band2: colorSet.getIndex(2).hex.substring(1),
    band3: colorSet.getIndex(4).hex.substring(1),
    band1End: '',
    band2End: '',
  }

  dataItems = this.props.data.dataItemDefinitions.array;

  componentDidMount() {
    // populates fields when editing
    const { tile } = this.props.ui.modal;
    if(tile && !tile.properties.type.default) {
      const { dataItem, name, min, max, unit, band1, band1End, band2, band2End, band3 } = tile.properties;
      this.setState({
        dataItem,
        name,
        min,
        max,
        unit,
        band1,
        band1End,
        band2,
        band2End,
        band3
      });
    }
  }

  handleChange = (e) => {
    e.preventDefault();
    const {addData} = this.props;
    this.setState({[e.target.name]: e.target.value}, () =>{
      addData(this.state);
    });
  }

  render() {
    const { name, dataItem, min, max, unit, band1, band1End, band2, band2End, band3 } = this.state;

    return (
      <div className='component-form'>
        <Dropdown
          style={{width:'100%'}}
          optionLabel={'label'}
          name='dataItem'
          value={dataItem}
          onChange={this.handleChange}
          options={this.dataItems}
          placeholder='Select a data item'
          filter={true}
          filterBy='label'
          required={true}
        />
        {/*<span className='p-float-label'>
          <InputText
            value={name}
            type='text'
            onChange={this.handleChange}
            name='name'
          />
          <label htmlFor='name'>Title</label>
        </span>*/}
        <span className='p-float-label'>
          <InputText
            value={unit}
            type='text'
            onChange={this.handleChange}
            name='unit'
          />
          <label htmlFor='unit'>Unit</label>
        </span>
        <span className='p-float-label'>
          <InputText
            value={min}
            type='number'
            onChange={this.handleChange}
            name='min'
            required={true}
          />
          <label htmlFor='min'>Min*</label>
        </span>
        <span className='p-float-label'>
          <InputText
            value={max}
            type='number'
            onChange={this.handleChange}
            name='max'
            required={true}
          />
          <label htmlFor='max'>Max*</label>
        </span>
        <MyColorPicker color={band1} handleChange={this.handleChange} label='Band 1:' name='band1'/>
        <div className='dialgauge-band-lengths'>
          <span className=''>{min === '' ? 'min to ' : `${min} to `}</span>
          <InputText
            value={band1End}
            style={{width:'50%'}}
            type='number'
            onChange={this.handleChange}
            name='band1End'
            placeholder='Band 1 End*'
          />
        </div>
        <MyColorPicker color={band2} handleChange={this.handleChange} label='Band 2:' name='band2' />
        <div className='dialgauge-band-lengths'>
          <span className=''>{band1End === '' ? 'Band 1 End to ' : `${band1End} to `}</span>
              <InputText
                value={band2End}
                style={{width:'50%'}}
                type='number'
                onChange={this.handleChange}
                name='band2End'
                placeholder='Band 2 End*'
              />
        </div>
        <MyColorPicker color={band3} handleChange={this.handleChange} label='Band 3:' name='band3' />
        <div className='dialgauge-band-lengths'>
          <span className=''>{band2End === '' ? 'Band 2 End to ' : `${band2End} to `}</span>
          <span className=''> {max === '' ? 'max' : max} </span>
        </div>
      </div>
    );
  }
}

function mapStateToProps({data, ui}) {
  return {data, ui}
}

export default connect(mapStateToProps)(GaugeForm);

